@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600,700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
  background: #2c3e50;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #4ca1af, #2c3e50);
  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#4ca1af), to(#2c3e50));
  background: -webkit-linear-gradient(right, #4ca1af, #2c3e50);
  background: linear-gradient(to left, #4ca1af, #2c3e50);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ }

*,
*::after,
*::before {
  box-sizing: border-box; }

.App {
  width: 95vw;
  margin: 0 auto;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center; }

.App-header {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 50px;
  margin: 30px 0;
  color: #e6f4f1; }
  .App-header__settings {
    display: flex;
    justify-content: space-between;
    width: 25%; }
    .App-header__settings span {
      display: flex;
      flex-direction: column;
      text-align: center;
      width: 45%; }
    .App-header__settings input {
      position: relative;
      -webkit-appearance: none;
      width: 100%;
      height: 10px;
      background: #e6f4f1;
      outline: none;
      opacity: 0.5;
      -webkit-transition: 0.2s;
      -webkit-transition: opacity 0.2s;
      transition: opacity 0.2s;
      margin-bottom: 10px;
      border-radius: 10px; }
    .App-header__settings input:hover {
      opacity: 1; }
    .App-header__settings input::-webkit-slider-thumb {
      -webkit-appearance: none;
      appearance: none;
      width: 20px;
      height: 20px;
      border-radius: 10px;
      background: #159957;
      cursor: pointer; }
  .App-header .step-counter {
    display: flex;
    align-items: center;
    font-size: 25px;
    font-weight: 500;
    width: 10%; }
    .App-header .step-counter p {
      margin: 0 20px 0 0; }
  .App-header__algorithms {
    display: flex;
    justify-content: space-evenly;
    width: 50%; }
    .App-header__algorithms button {
      position: relative;
      background: none;
      border: none;
      padding: 12px 16px;
      color: #e6f4f1;
      font-weight: 500;
      font-size: 16px;
      font-family: "Montserrat", sans-serif; }
    .App-header__algorithms button:disabled,
    .App-header__algorithms button[disabled] {
      opacity: 0.25;
      cursor: wait; }
    .App-header__algorithms button::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      top: 7px;
      right: 6px;
      background-color: #26394b;
      z-index: -1;
      -webkit-transition: -webkit-transform 0.15s ease-out 0s;
      transition: -webkit-transform 0.15s ease-out 0s;
      transition: transform 0.15s ease-out 0s;
      transition: transform 0.15s ease-out 0s, -webkit-transform 0.15s ease-out 0s; }
    .App-header__algorithms button::after {
      content: "";
      left: 0px;
      top: 0px;
      position: absolute;
      width: 100%;
      height: 100%;
      opacity: 1;
      border-width: 1.5px;
      border-style: solid;
      border-color: #e6f4f1;
      -webkit-transition: -webkit-transform 0.15s ease-out 0s;
      transition: -webkit-transform 0.15s ease-out 0s;
      transition: transform 0.15s ease-out 0s;
      transition: transform 0.15s ease-out 0s, -webkit-transform 0.15s ease-out 0s; }
    .App-header__algorithms button:hover {
      cursor: pointer; }
      .App-header__algorithms button:hover:disabled {
        cursor: wait; }
      .App-header__algorithms button:hover:before {
        -webkit-transform: translate(7px, -6px);
                transform: translate(7px, -6px); }
      .App-header__algorithms button:hover:after {
        -webkit-transform: translate(-7px, 6px);
                transform: translate(-7px, 6px); }

.App-container {
  width: 100%;
  height: 85vh;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: row; }

.Container-column {
  margin: 0 1px;
  background: #e6f4f1;
  box-shadow: 10px 10px 15px -5px rgba(0, 0, 0, 0.5); }

